body {
  background-color: #a9a9a9;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding: 0 50px;
  font-style: normal;
  list-style: none;
  display: flex;
}

.img {
  align-items: center;
  width: 40px;
  height: 40px;
  display: flex;
}

nav ul {
  list-style: none;
  display: flex;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: block;
  position: relative;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.nav__item {
  color: #000;
  text-decoration: none;
  display: flex;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
